$diameter-stopwatch: 400px;
$height-second-hand: $diameter-stopwatch / 2;
$with-second-hand: $diameter-stopwatch / 100;
$positioning-second-hand: $height-second-hand - ($with-second-hand / 2);
$height-minute-hand: $height-second-hand / 2;
$with-minute-hand: $diameter-stopwatch / 50;
$positioning-minute-hand: $height-second-hand - ($with-minute-hand / 2);
$differens-height-hands: $height-second-hand - $height-minute-hand;
